        <h2 class="posting--title">
          디지털 트랜스포메이션 - CDP(Customer Data Platform)
        </h2>
        <div class="posting--context" >

          <p><img src="https://www.eluocnc.com/userfiles/editor/202207/20220712161806462.jpg"><br><br><br><b>고객 데이터를 비즈니스 가치로 전환 시키기 위한 David
              Roger의 4가지 템플릿</b></p>
          <p class="MsoNormal"><br></p>
          <p class="MsoNormal"><br>1. 통찰 (Insight)</p>
          <p class="MsoNormal">고객 데이터를 통해 이전에는 보이지 않았던 관계, 패턴, 영향력을 찾아냄으로써 비즈니스에 가치를 더한다는 것인데요. 수집된 데이터의 분석을 통해 1)시장에서 자사
            브랜드/제품/서비스에 대한 인식은 어떠한지 2)고객 결정의 동기와 영향은 무엇이 있는지 3)고객의 입소문을 예측하고 측정을 할 수 있는지에 대한 답을 얻고자 하는 것입니다.</p>
          <p class="MsoNormal"><br></p>
          <p class="MsoNormal">2. 집중 (Targeting)</p>
          <p class="MsoNormal">그동안 디지털 트랜스포메이션에 대해 다루면서 가장 중요한 것은 기술/테크놀로지가 아니라 사람/소비자라는 것을 자주 이야기 했습니다. 그렇다면 사업과 관련있는 잠재
            소비자 세그멘테이션(Segmentation)을 통해, 기업이 집중을 해야할 고객군이 어디인지를 파악하는 것이 필요합니다. 과거의 세그멘테이션이 연령, 거주지, 제품 사용 여부를 포함한 소수의
            요건이었다면, 이제는 어떤 이메일을 클릭했는지 어떤 보상을 받고 어떤 콘텐츠를 공유했는지, 또 그들의 CLV(Customer Lifetime Value)는 어떤지 등 고객의 거의 모든 일거수일투족을
            추적 분석하고 있습니다.</p>
          <p class="MsoNormal"><br><img src="https://www.eluocnc.com/userfiles/editor/202207/20220712133917919.jpg"><br></p>
          <p class="MsoNormal"><span style="font-size: 14px;">[fig1] CLV 계산 방식</span></p>
          <p class="MsoNormal"><span style="font-size: 14px;">출처 : CleverTap.com, “Customer Lifetime Value: What is it and
              How to Calculate"</span></p>
          <p class="MsoNormal"><sup><b><br><br></b></sup></p>
          <p class="MsoNormal"><sup><b><br></b></sup></p>
          <p class="MsoNormal">3. 맞춤 (Personalization)</p>
          <p class="MsoNormal">빅데이터 시대, 맞춤 서비스의 대표 사례로는 British Airways의 'Know Me' 프로그램을 들 수 있습니다. 모든 승객을 대상으로 하는 것은 아니지만,
            퍼스트 클래스 탑승객에 관해서는 구글/소셜네트워크 등을 포함한 모든 데이터를 취합 분석하여, 탑승 전에 이 승객이 퍼스트클래스를 처음 이용하는 승객인지, 취미 활동 및 관심 분야는 무엇인지, 식단에
            특이점은 없는지 등을 미리 알고 탑승 시 이에 맞춘 서비스를 한다는 것입니다. 최근 기업에는 이러한 식의 방대한 고객데이터를 축적, 분석하는 시스템을 구축 및 운영하고 있는데, 이를
            'Customer Data Platform(CDP)'라고 합니다.</p>
          <p class="MsoNormal"><br></p>
          <p class="MsoNormal">4. 평가 (Context)</p>
          <p class="MsoNormal">원문의 Context(맥락)을 국내 참고 서적은 ‘평가’로 번역하고 있습니다. 단순히 단어만 봐서는 쉽게 이해가 되지 않는 내용인데요. 평가는 한 고객이 쌓아 올린
            행동, 결과를 더 많은 다른 사람들과 비교함으로써 기준을 제시하고 기업과 고객에게 새로운 가치를 제공한다는 것입니다. 나이키+(플러스) 플랫폼을 보면 회원 한사람의 데이터를 기록하는 것이 아닌 쌓인
            데이터를 같은 그룹 사용자들의 기록, 목표와 비교함으로써 자신의 운동 활동에 대한 평가를 할 수 있도록 해주는 것을 생각하시면 됩니다.</p>
          <p class="MsoNormal"><br><br><br></p>
          <p class="MsoNormal"><b>CDP, 고객에 대한 모든 데이터</b></p>
          <p class="MsoNormal">위에서 잠시 언급한 CDP(Customer Data Platform)는 가능한 모든 채널에서 고객의 정보를 수집하여, 해당 고객에 대한 통합된 뷰를 제공하고 이를
            기반으로 하이퍼-개인화된(Hyper-Personalized) 고객경험을 제공할 수 있도록 해주는 디지털 플랫폼입니다.1 CDP연구소(CDP Institute)는 CDP를 타 시스템이 접속(이용)할
            수 있는 지속적이고 통합된 고객데이터를 제공하는 패키지 소프트웨어로 정의하고 있습니다.2 CDP를 설명하는데 주요한 키워드를 살펴보겠습니다.</p>
